// ** Initial State
import {
  DELETE_PRODUCT,
  EDIT_PRODUCT, GET_ALL_PRODUCTS,
  GET_CATEGORIES,
  GET_GROUP_TYPES,
  GET_GROUP_TYPES_DEFAULT,
  GET_PRODUCT, GET_PRODUCT_VARIANT,
  GET_PRODUCTS,
  UPLOAD_IMAGES,
  UPLOAD_VIDEOS
} from "../action"

const initialState = {
  products: [],
  allProducts: [],
  categories: [],
  total: 0,
  params: {},
  product: null,
  variants: [],
  type_values: [],
  default_type_values: [],
  images: [],
  video: null
}

const products = (state = initialState, action) => {
  switch (action.type) {
    case GET_CATEGORIES:
      const categoryConverts = []
        categoryConverts.push({value: '', label: 'Nhóm sản phẩm'})
        action?.categories?.forEach(category => {
          categoryConverts.push({value: category.id, label: category.category})
        })
      return {...state, categories: categoryConverts}
    case GET_PRODUCTS:
      return {
        ...state,
        products: action.products,
        total: action.totalPages,
        params: action.params
      }
    case GET_ALL_PRODUCTS:
      return {
        ...state,
        allProducts: action.allProducts
      }
    case GET_PRODUCT:
      const default_type_values = action.default_type_values
      default_type_values.forEach(type_value => {
        const product_types = []
        type_value?.product_types.forEach(product_type => {
          product_types.push({value: product_type.id, label: product_type.name})
        })
        type_value.product_type_converts = product_types
      })
      const type_values = action.type_values
      type_values.forEach(type_value => {
        const product_types = []
        type_value?.product_types.forEach(product_type => {
          product_types.push({value: product_type.id, label: product_type.name})
        })
        type_value.product_type_converts = product_types
      })

      return { ...state,
        product: action.product,
        type_values: type_values,
        default_type_values: default_type_values,
        variants: action.variants
      }
    // case GET_PRODUCT_VARIANT:
    //   return { ...state, variants: action.variants }
    case EDIT_PRODUCT:
      return { ...state }
    case DELETE_PRODUCT:
      return { ...state }
    // case GET_GROUP_TYPES:
    //   const type_values = action.type_values
    //   type_values.forEach(type_value => {
    //     const product_types = []
    //     type_value?.product_types.forEach(product_type => {
    //       product_types.push({value: product_type.id, label: product_type.name})
    //     })
    //     type_value.product_type_converts = product_types
    //   })
    //   return {
    //     ...state,
    //     type_values: type_values
    //   }
    // case GET_GROUP_TYPES_DEFAULT:
    //   const default_type_values = action.default_type_values
    //   default_type_values.forEach(type_value => {
    //     const product_types = []
    //     type_value?.product_types.forEach(product_type => {
    //       product_types.push({value: product_type.id, label: product_type.name})
    //     })
    //     type_value.product_type_converts = product_types
    //   })
    //   return {
    //     ...state,
    //     default_type_values: default_type_values
    //   }
    case UPLOAD_IMAGES:
      return {
        ...state,
        images: action.images
      }
    case UPLOAD_VIDEOS:
      return {
        ...state,
        video: action.video
      }
    default:
      return { ...state }
  }
}
export default products
