// ** Initial State
import {BLOCK_CUSTOMER, EDIT_CUSTOMER, GET_CUSTOMER, GET_CUSTOMERS} from "../action"

const initialState = {
  customers: [],
  total: 0,
  params: {},
  customer: null,
  blockStatus: false
}

const customers = (state = initialState, action) => {
  switch (action.type) {
    case GET_CUSTOMERS:
      return {
        ...state,
        customers: action.customers,
        total: action.totalPages,
        params: action.params
      }
    case GET_CUSTOMER:
      return { ...state, customer: action.customer }
    case EDIT_CUSTOMER:
      return { ...state, customer: action.customer }
    case BLOCK_CUSTOMER:
      return { ...state, blockStatus: action.blockStatus }
    default:
      return { ...state }
  }
}
export default customers
